import React, { Component } from 'react'

const SkillBar = (props)=>{
  return (
    <div className="skill-bar">
        <div className="progress">
        <div
          className="progress-bar progress-bar-black"
          role="progressbar"
          aria-valuenow={props.value}
          aria-valuemin="0"
          aria-valuemax="100"
          style={{width: `${props.value}%`}}>
            <span className="percent">{props.value}%</span>
        </div>
        </div>
        <h6 className="skill-title">{props.title}</h6>
    </div>
  );
}

export default class Skills extends Component {
  render() {
    return(
      <div className="resume-box">
        <h4 className="title-bar title-bar-black title-bar-hover"><strong>Skills</strong> &amp; Abilities</h4>
        <div className="box-content">
          <SkillBar title="Adobe Photoshop & Adobe Lightroom" value={80} />
          <SkillBar title="C/C++" value={60} />
          <SkillBar title="PHP" value={80} />
          <SkillBar title="NodeJS/Javascript" value={40} />
          <SkillBar title="CSS/HTML/Jquery" value={80} />
        </div>
    </div>

    )
  }
};