import React, { Component  } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleLeft,faAngleRight } from '@fortawesome/free-solid-svg-icons';
import posed from 'react-pose';
import wrapComponent from '../../store';
import {ButtonToggle} from '../MainMenu';

const HOME_PAGE = "#";

const NavDiv = posed.nav({
  hiddenNav: {
    applyAtEnd: {
      visibility: "hidden"
    },
    opacity: 0,
  },
  visibleNav: {
    applyAtStart:{
      visibility: "visible"
    },
    opacity: 1,
  },
});

const NavButtonPose = posed.div({
  show: {
    transition: { duration: 300 },
    applyAtStart:{
      display: "block"
    },
    opacity: 1,
  },
  hide: {
    transition: { duration: 300 },
    applyAtStart: {
      display: "none"
    },
    opacity: 0,
  },
});

class Nav extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isVisible: false,
      navVisible: true
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.id !== this.props.id) {
      this.setState({
        isVisible: (nextProps.id>0?true:false),
        navVisible: false
      });
    }
  }

  render() {
    const isVisible = this.state.isVisible; 
    var caption = this.props.caption;
    return (
      <NavDiv id="main-nav" pose={isVisible ? 'visibleNav' : 'hiddenNav'}>
        <ButtonToggle className="menu-toggle-desktop"/>
        <div className="text-right vertical-center">
          <div className="nav-text">
            <div className="my-avatar">
              <img src="./my-avatar.jpg" className="rounded-circle" alt="avatar"/>
            </div>
            <h2 className="margin-bottom-0">{this.props.title}</h2>
            <h6 className="nav-caption" dangerouslySetInnerHTML={{__html: caption}}>
            </h6>
          </div>
        </div>
        <NavButtonPose className="nav-buttons" pose={this.state.navVisible?'show':'hide'} onPoseComplete={
          () => this.setState({navVisible: true})
        }>
          <a href={HOME_PAGE} className="btn-prev"
          onClick={
            () => this.props.PrevSlide()
          }
          style={{display:(this.props.id<=0)?'none':'block'}}
          >
            <FontAwesomeIcon icon={faAngleLeft}/>
            <span>{this.props.prevTitle}</span>
          </a>
          <a href={HOME_PAGE} className="btn-next"
          onClick={
            () => this.props.NextSlide()
          }
          style={{display:(this.props.id>=2)?'none':'block'}}
          >
            <span>{this.props.nextTitle}</span>
            <FontAwesomeIcon icon={faAngleRight}/>
          </a>
        </NavButtonPose>
      </NavDiv>
    )
  }
}

export default wrapComponent(Nav);