import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebookF,faTumblr ,faInstagram } from '@fortawesome/free-brands-svg-icons';

const Item = (props)=>{
    return (
        <a href={props.link} className={`social-icon icon-${props.social}`}>
            <FontAwesomeIcon icon={props.icon}/>
        </a>
    );
}

export default class SocialMedia extends Component {
  render() {
    return(
        <div className="contact-box">
            <h4 className="title-bar title-bar-black"><strong>Social</strong> media</h4>
            <div className="social-icons margin-bottom-10">
                <Item link="https://fb.com/nhocksoc" social="facebook" icon={faFacebookF}/>
                <Item link="http://nhocksoc.tumblr.com/" social="tumblr" icon={faTumblr}/>
                <Item link="https://www.instagram.com/nhocksoc" social="instagram" icon={faInstagram}/>
            </div>
        </div>
    )
  }
};