import React from "react";

const X = "#";

const AboutMe = ({props})=>{
  return(
      <div className="resume-box">
          <h4 className="title-bar title-bar-black title-bar-hover"><strong>About</strong> Me</h4>
          <div className="box-content">
              <p className="lead">I’m <strong>Lâm Thành Lộc</strong> - 20 years old developer in Vietnam.</p>
              <p>Through <strong>5 years of my expierience</strong> I have been working for over <strong>10+ happy clients</strong> from all over the country. I’m always looking for new, interesting collaborations and I’m sure that I’m the man you need to hire!</p>
              <a href={X} className="btn btn-primary" onClick={
                () => this.props.NextSlide()
              }>Contact Me</a>
          </div>
      </div>
  );
};

export default AboutMe;