import React,{Component} from 'react';
import {connect} from "react-redux";
import { ToggleMenu,UpdateTitle,GoSlide } from '../actions';


const HOME_PAGE = "#";

const Button = (props) =>{
    return (
        <a href={HOME_PAGE} className={`menu-toggle ${props.className}`} onClick={
            ()=>props.ToggleMenu()
        }>
        </a>
    )
}

export const ButtonToggle = connect(null,
    dispatch => ({
        ToggleMenu: () => dispatch(ToggleMenu())
    }))(Button);

class MainMenu extends Component {

    render() {
        const isVisible = this.props.isVisible;
        return (
            <div id="main-menu" className={`dark ${(isVisible?'active':'')}`}>
                <a href={HOME_PAGE} id="menu-close" onClick={()=>this.props.ToggleMenu()}>
                </a>
                <ul className="nav nav-menu vertical-center flex-column">
                    <li className="nav-item"><a className="nav-link" href={HOME_PAGE} onClick={()=>this.props.GoSlide(1)}>Resume</a></li>
                    <li className="nav-item"><a className="nav-link" href={HOME_PAGE} onClick={()=>this.props.GoSlide(2)}>Contact</a></li>
                </ul>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    isVisible: state.menu
});

const mapDispatchToProps = dispatch =>({
    ToggleMenu: ()=>dispatch(ToggleMenu()),
    GoSlide: (id) => {dispatch(UpdateTitle(null,null)); dispatch(GoSlide(id))},
})

export default connect(mapStateToProps,mapDispatchToProps)(MainMenu);