import React, { Component } from 'react'

const Event = (props)=>{
  return (
    <div className="timeline-event te-black">
        <div className="event-date">{props.start} - {props.end}</div>
        <h4 className="event-name">{props.title}</h4>
        <div className="event-description ">{props.description}</div>
    </div>
  );
}

export default class Education extends Component {
  render() {
    return(
        <div className="resume-box">
            <h4 className="title-bar title-bar-black title-bar-hover"><strong>Education</strong> &amp; Jobs</h4>
            <div className="box-content">
                <div className="timeline">
                    <Event start={2018} end={2019}
                        title={[<strong>Frontend</strong>, " developer"]}
                        description="Kakka Inc, Vietnam"/>
                    <Event start={2017} end={2021}
                        title={[<strong>Infomation</strong>," technology"]}
                        description="University of Science - VNU, Vietnam"/>
                </div>
            </div>
        </div>
    )
  }
};