import React, { Component } from 'react'
import wrapComponent from '../../store';
import posed from 'react-pose';
import Nav from "./Nav"

const HOME_PAGE = "#";

const HomeDiv = posed.div({
  hidden: {
    width: '380px',
    transition: { duration: 1200 }
  },
  visible: {
    width: '100%',
    transition: { duration: 1200 }
  },
});

const HomeContentDiv = posed.div({
  hidden: {
    marginLeft: '-600px',
    transition: { duration: 1200 }
  },
  visible: {
    marginLeft: '0px',
    transition: { duration: 1200 }
  }
});


class Home extends Component {

  constructor(props) {
    super(props);
    this.state = { isVisible: true };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.id !== this.props.id) {
      this.setState({isVisible: (nextProps.id===nextProps.slideId?true:false)});
    }
  }

  render() {
    const { isVisible } = this.state;
    return (
      <HomeDiv id="home" className={(isVisible ? '' : 'home-hidden')} pose={isVisible ? 'visible' : 'hidden'}>
        <div className="home-wrapper">
          <HomeContentDiv
            className="home-content home-photo main-photo dark"
            pose={isVisible ? 'visible' : 'hidden'}
            style={{backgroundImage: `url('./main-photo.jpg')`}}
          >
            <div className="overlay overlay-black"></div>
            <div className="text-center vertical-center">
              <span className="home-title">Lâm Thành <strong>Lộc</strong></span>
              <span className="home-caption"> <strong>Developer</strong></span>
            </div>
          </HomeContentDiv>
          <a href={HOME_PAGE} id="btn-start" onClick={
            () => this.props.NextSlide()
          }>
          </a>  
          <Nav title={['Lâm Thành ',<strong>Lộc</strong>]}/>         
        </div>
      </HomeDiv> 
    )
  }
}

export default wrapComponent(Home);