import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMusic,faFighterJet ,faCoffee } from '@fortawesome/free-solid-svg-icons';

const Item = (props)=>{
  return (
    <div className="col-sm-4 icon-box text-center">
        <span className="icon icon-circle icon-sm icon-black"><FontAwesomeIcon icon={props.icon}/></span>
        <h6 className="font-alt margin-bottom-0">{props.title}</h6>
    </div>
  );
}

export default class Hobbies extends Component {
  render() {
    return(
        <div className="resume-box">
            <h4 className="title-bar title-bar-black title-bar-hover"><strong>Hobbies</strong> &amp; Interests</h4>
            <div className="box-content">
                <div className="row">
                    <Item icon={faMusic} title="Indie Music"/>
                    <Item icon={faFighterJet} title="Travelling"/>
                    <Item icon={faCoffee} title="Coffee"/>
                </div>
            </div>
        </div>
    )
  }
};