export const NextSlide = () => ({
    type: 'NEXT_SLIDE'
});

export const PrevSlide = () => ({
    type: 'PREV_SLIDE'
})

export const GoSlide = (id) => ({
    type: 'GO_SLIDE',
    id
})

export const ToggleMenu = () => ({
    type: 'TOGGLE_MENU',
})

export const UpdateCaption = (text) => ({
    type: 'UPDATE_CAPTION',
    caption: text
})

export const UpdateTitle = (prev,next) => ({
    type: 'UPDATE_TITLE',
    prev,
    next
})
