import { combineReducers } from 'redux';

const MIN_ID = 0, MAX_ID=2;

const id = (id = 0, action) => {
    switch (action.type) {
        case 'NEXT_SLIDE':
            return (id<MAX_ID?++id:id);
        case 'PREV_SLIDE':
            return (id>MIN_ID?--id:id);
        case 'GO_SLIDE':
            return action.id;
        default:
            return id
    }
};

const caption = (caption = 0, action) => {
    switch (action.type) {
        case 'UPDATE_CAPTION':
            return action.caption
        default:
            return caption
    }
};

const title = (state={}, action) => {
    switch (action.type) {
        case 'UPDATE_TITLE':
            return {
                prev: action.prev,
                next: action.next
            };
        default:
            return state;
    }
};

const menu = (state = false, action) => {
    switch (action.type){
        case 'TOGGLE_MENU':
            return !state;
        default:
            return state;
    }
}


export default combineReducers({
    id,
    caption,
    title,
    menu
})