import React, { Component } from 'react';
import Resume from './Resume';
import Contact from './Contact';
import {ButtonToggle} from './MainMenu';
import posed from 'react-pose';
import wrapComponent from '../store';

const SectionsDiv = posed.div({
  hidden: {
    applyAtEnd: {
      visibility: "hidden"
    },
    opacity: 0,
  },
  visible: {
    applyAtStart:{
      visibility: "visible"
    },
    opacity: 1,
  }  
});

class Sections extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isVisible: false,
      width:0
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.id !== this.props.id) {
      if(nextProps.id>0){
        let screenWidth = window.innerWidth - (window.innerWidth>=768?380:0);
        this.setState({
          isVisible: true,
          width: -(screenWidth)*(nextProps.id-1)
        });
      }
      else{
        this.setState({
          isVisible: false,
          width: 0
        });
      }
    }
  }

  render() {
    const isVisible = this.state.isVisible;
    return (
        <SectionsDiv id="sections" pose={isVisible ? 'visible' : 'hidden'}>
          <div class="section-container"  style={{transform: `translate3d(${this.state.width}px, 0px, 0px)`}}>
            <Resume slideId={1} />
            <Contact slideId={2} />
          </div>
          <ButtonToggle className="menu-toggle-mobile"/>
        </SectionsDiv>
        );
  }
}

export default wrapComponent(Sections);
