import React, { Component } from 'react';
import SocialMedia from './SocialMedia';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMapMarked,faEnvelope } from '@fortawesome/free-solid-svg-icons';
import wrapComponent from '../../store';

const Item = (props)=>{
    return(
        <li>
            <FontAwesomeIcon className="text-primary" fixedWidth={true} icon={props.icon}/>
            <span className="content">{props.content}</span>
        </li>
    )
}

const TITLE = "Contact Me";
const CAPTION = `Do not hestiate to <span>contact me</span>`;

class Contact extends Component {

    componentWillReceiveProps(nextProps) {
        if (nextProps.id !== this.props.id) {
            if(nextProps.id+1 === this.props.slideId){
                this.props.UpdateTitle(this.props.prevTitle,TITLE);
            }
            if(nextProps.id-1 === this.props.slideId){
                this.props.UpdateTitle(TITLE,this.props.nextTitle);
            }
            if(nextProps.id === this.props.slideId){
                this.props.UpdateCaption(CAPTION);
            }
        }
    }

    render() {
    return (
        <section id="contact">
            <div className="section-wrapper">
                <div className="contact-content padded">
                    <div className="contact-box">
                        <h4 className="title-bar title-bar-black"><strong>Contact</strong> me</h4>
                        <div className="box-content">
                            <ul className="list-icons">
                                <Item content="Dormitory of VNUHCM, Vietnam" icon={faMapMarked}/>
                                <Item content="contact@lamthanhloc.com" icon={faEnvelope}/>
                            </ul>
                        </div>
                    </div>
                    <SocialMedia/>
                </div>
            </div>
        </section>
    )
    }
}

export default wrapComponent(Contact);