import { connect } from "react-redux";
import {NextSlide,PrevSlide,UpdateCaption,UpdateTitle,GoSlide} from "./actions"

export default function wrapComponent(Component){

    const mapStateToProps = state => ({
        id: state.id,
        caption: state.caption,
        prevTitle: state.title.prev,
        nextTitle: state.title.next
    });

    const mapDispatchToProps = dispatch =>({
        NextSlide: () => {dispatch(UpdateTitle(null,null)); dispatch(NextSlide())},
        PrevSlide: () => {dispatch(UpdateTitle(null,null)); dispatch(PrevSlide())},
        GoSlide: (id) => {dispatch(UpdateTitle(null,null)); dispatch(GoSlide(id))},
        UpdateCaption: (text) => dispatch(UpdateCaption(text)),
        UpdateTitle:(prev,next) => dispatch(UpdateTitle(prev,next)),
    })

    return connect(
        mapStateToProps,
        mapDispatchToProps
    )(Component);
    
}
